
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
  display: grid;
  grid: [row1-start] "header header header" 1fr [row1-end] [row2-start] "main main main" [row2-end] [row3-start] "footer footer footer" 25px [row3-end]/auto 50% auto;
}
* {
  padding: 0;
  margin: 0;
}

.navbar {
  grid-area: header;
}

.blue {
  color: #258def;
}
.homepage {
  grid-area: main;
  color: white;
}
.homepage--landing {
    background: -webkit-gradient(linear, left top, left bottom, from(#007CE9), to(#70BBFC));
    background: linear-gradient(to bottom, #007CE9 0%, #70BBFC 100%);
}
.homepage--landing--texts {
      opacity: 0;
      margin-left: 7vw;
}
.homepage--landing--texts.fade-in {
        opacity: 1;
        -webkit-animation-name: fadeInOpacity;
                animation-name: fadeInOpacity;
        -webkit-animation-iteration-count: 1;
                animation-iteration-count: 1;
        -webkit-animation-timing-function: ease-in;
                animation-timing-function: ease-in;
        -webkit-animation-duration: 2s;
                animation-duration: 2s;
}
@-webkit-keyframes fadeInOpacity {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
@keyframes fadeInOpacity {
0% {
    opacity: 0;
}
100% {
    opacity: 1;
}
}
.homepage--landing--texts .name, .homepage--landing--texts .description {
        font-size: 2rem;
        color: #deeef5;
}
.homepage--landing--texts .name {
        padding-top: 2rem;
        margin-bottom: 20vh;
}
.homepage--landing--texts .description {
        padding-top: 1rem;
}
.homepage--landing--texts .position-title {
        font-size: 4rem;
}
.homepage--landing--texts .position-title:after {
          content: ' ';
          display: block;
          width: 50px;
          border: 2px solid #fff;
}
.homepage--about {
    color: #258def;
    height: 100%;
}
.homepage--about .text-section {
      padding-left: 5vw;
}
.homepage--about .person {
      font-size: 4rem;
      padding: 0;
      margin: 0 -1rem;
}
.homepage--experience {
    background: -webkit-gradient(linear, left top, left bottom, from(#007CE9), to(#70BBFC));
    background: linear-gradient(to bottom, #007CE9 0%, #70BBFC 100%);
}
.homepage--education {
    background: -webkit-gradient(linear, left top, left bottom, from(#007CE9), to(#70BBFC));
    background: linear-gradient(to bottom, #007CE9 0%, #70BBFC 100%);
}
.homepage--social-media {
    background: -webkit-gradient(linear, left top, left bottom, from(#007CE9), to(#70BBFC));
    background: linear-gradient(to bottom, #007CE9 0%, #70BBFC 100%);
}
.homepage .hp-section {
    height: 100vh;
    width: 100vw;
}
.homepage .title .section-name {
    padding-top: 2vw;
    font-size: 2rem;
}
.homepage .title .section-name:after {
      content: ' ';
      display: block;
      width: 50px;
      border: 2px solid;
}
